html {
  box-sizing: border-box;
}

*, ::before, ::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

.header {
  background-color: #26DFD0;
  height: 10%;
}

.menu {
  list-style: none;
  display: flex;
  justify-content: space-between;
  font-family: "Libre Baskerville", serif;
  color: white;
  padding: 20px 40px;
  margin: 0;
  gap: 10px;
}

.menu__item {
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
}

.menu__link {
  text-decoration: none;
  color: white;
}

.hero {
  height: 80vh;
  width: 100%;
  background-image: url(/fotky-na-web/uvodni-foto.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-areas: "-" "hero__title" "hero__button";
}
@media (max-width: 500px) {
  .hero {
    height: 60vh;
  }
}
.hero__title {
  margin: 0;
  font-family: "League Spartan", sans-serif;
  color: #F9D030;
  font-size: 100px;
  margin-bottom: 20px;
  text-align: center;
  grid-area: hero__title;
  align-self: end;
  justify-self: center;
}

.hero__button {
  width: fit-content;
  padding: 20px;
  font-size: 24px;
  font-family: "Libre Baskerville", serif;
  color: white;
  background-color: #F62AA0;
  border-radius: 15px;
  border: none;
  grid-area: hero__button;
  align-self: start;
  justify-self: center;
  text-decoration: none;
}

.footer {
  background-color: #F62AA0;
  color: white;
  font-family: "Libre Baskerville", serif;
  font-style: italic;
  text-align: center;
  padding: 15px;
  font-size: 30px;
  height: 10%;
}

.grid__programmes {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 300px);
  grid-auto-flow: column;
  gap: 0;
}

.grid__picture {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.grid__item {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: "Libre Baskerville", serif;
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  line-height: 60px;
  margin: 0;
}
.grid__item--blue {
  background-color: #26DFD0;
}
.grid__item--yellow {
  background-color: #F9D030;
}
.grid__item--pink {
  background-color: #F62AA0;
}
.grid__item--green {
  background-color: #B8EE30;
}

.grid__item a {
  text-decoration: none;
  color: white;
}

.title__heading {
  font-family: "League Spartan", sans-serif;
  color: #F9D030;
  text-align: center;
  font-size: 64px;
}
.title__heading--left {
  text-align: left;
  padding-left: 40px;
}
.title__heading--right {
  text-align: right;
  padding-right: 60px;
  padding-top: 60px;
}

.title__subheading {
  font-family: "League Spartan", sans-serif;
  font-weight: 400;
  font-size: 48px;
  padding-left: 40px;
}

.title__subsubheading {
  font-weight: normal;
  font-family: "Libre Baskerville", serif;
  font-style: italic;
  font-size: 24px;
  padding-left: 40px;
}
.title__subsubheading--bold {
  font-style: normal;
  font-weight: bold;
}

.info {
  display: flex;
}
.info__symbol {
  width: 100px;
  height: 100px;
}
.info__item {
  display: flex;
  gap: 20px;
  font-family: "Libre Baskerville", serif;
  font-style: italic;
  align-items: center;
  font-size: 20px;
}
.info__button {
  background-color: #F9D030;
  text-decoration: none;
  color: black;
  font-family: "Libre Baskerville", serif;
  font-size: 20px;
  display: flex;
  gap: 10px;
  padding: 5px;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.button__arrow {
  width: 20px;
  height: 20px;
}

.info__description {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 1fr);
  width: 100%;
  padding: 40px;
  position: relative;
}
.info__description--green {
  background-color: #B8EE30;
}
.info__description--blue {
  background-color: #26DFD0;
}
.info__description--yellow {
  background-color: #F9D030;
}

.teacher-info {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1fr);
  grid-template-areas: "title photo" "info photo";
  margin-left: 30px;
}

.title {
  grid-area: title;
}

.teacher-info__text {
  grid-area: info;
  padding: 40px;
  padding-top: 60px;
  font-family: "Libre Baskerville", serif;
  line-height: 2;
  font-size: 24px;
}

.teacher-info__photo {
  grid-area: photo;
  width: auto;
  height: 600px;
  border-radius: 100%;
  padding: 40px 0;
  align-self: center;
  justify-self: center;
}

.price {
  margin-right: 60px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas: "photo title" "photo info" "photo button";
}

.price__photo {
  grid-area: photo;
  height: 600px;
  width: auto;
  border-radius: 100%;
  padding: 40px 0;
  align-self: center;
  justify-self: center;
}

.price__info {
  grid-area: info;
  padding: 40px;
  font-family: "Libre Baskerville", serif;
  line-height: 2;
  font-size: 24px;
  text-align: right;
}

.price__button {
  grid-area: button;
  width: 250px;
  height: 90px;
  background-color: #F62AA0;
  border: none;
  border-radius: 20px;
  color: white;
  font-size: 24px;
  font-family: "Libre Baskerville", serif;
  justify-self: right;
  margin-right: 40px;
}

.references__text {
  font-family: "Libre Baskerville", serif;
  font-size: 26px;
  padding: 0 40px;
  font-style: italic;
}
.references__text--second {
  padding-top: 60px;
}